.page-main-example {
  background-color: #eaeaea;
  padding-top: 25px;
  &__title {
    font-family: Oswald;
    font-size: 36px;
    font-weight: 400;
    line-height: 40px;
    color: #000000;
  }
  &__list {
    text-align: center;
    color: #444444;
    font-family: Arial;
    font-size: 12px;
    font-weight: 400;
    line-height: 12px;

    @media (min-width: @tablet-width) {

      display: grid;


    };

  }

  &__index {
    font-family: Oswald;
    font-size: 24px;
    font-weight: 400;
    line-height: 24px;
    color: #000000;
  }

  &__cost {
    font-family: Oswald;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: #000000;
  }

  &__btn,
  &__btn--active {
    font-family: Oswald;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    text-align: left;
  }

  @media (min-width: @desctop-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
